import React from 'react';
import { Card, Row, Col, Steps, Icon, Button, message } from 'antd';
import './Navigation.less';
const Step = Steps.Step;

const steps = [{
  title: '',
  content: 'First-content',
}, {
  title: '',
  content: 'Second-content',
}, {
  title: '',
  content: 'Last-content',
}];

class StepsDemo extends React.Component {
  state = {
    current: 0,
  };

  next = () => {
    const current = this.state.current + 1;
    this.setState({ current });
  }
  prev = () => {
    const current = this.state.current - 1;
    this.setState({ current });
  }

  render() {

    const { current } = this.state;

    return (
      <div>
        <Row style={{ marginTop: '20px' }}>
          <Col span={24}>
            <Card hoverable title="">
              <Steps current={1}>
                <Step title="" description="" />
                <Step title="" description="" />
                <Step title="" description="" />
              </Steps>
            </Card>
          </Col>
        </Row>
        <Row style={{ marginTop: '20px' }}>
          <Col span={24}>
            <Card hoverable title="">
              <Steps size="small" current={1}>
                <Step title="" />
                <Step title="" />
                <Step title="" />
              </Steps>
            </Card>
          </Col>
        </Row>
        <Row style={{ marginTop: '20px' }}>
          <Col span={24}>
            <Card hoverable title="">
              <Steps>
                <Step status="finish" title="" icon={<Icon type="user" />} />
                <Step status="finish" title="" icon={<Icon type="solution" />} />
                <Step status="process" title="" icon={<Icon type="credit-card" />} />
                <Step status="wait" title="" icon={<Icon type="smile-o" />} />
              </Steps>
            </Card>
          </Col>
        </Row>
        <Row style={{ marginTop: '20px' }}>
          <Col span={24}>
            <Card hoverable title="">
              <Steps current={current}>
                {steps.map(item => <Step key={item.title} title={item.title} />)}
              </Steps>
              <div className="steps-content">{steps[this.state.current].content}</div>
              <div className="steps-action">
                {
                  this.state.current < steps.length - 1
                  &&
                  <Button type="primary" onClick={() => this.next()}></Button>
                }
                {
                  this.state.current === steps.length - 1
                  &&
                  <Button type="primary" onClick={() => message.success('!')}></Button>
                }
                {
                  this.state.current > 0
                  &&
                  <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
                    
                  </Button>
                }
              </div>
            </Card>
          </Col>
        </Row>
        <Row gutter={8} style={{ marginTop: '20px' }}>
          <Col span={12}>
            <Card hoverable title="">
              <Steps direction="vertical" current={1}>
                <Step title="" description="" />
                <Step title="" description="" />
                <Step title="" description="" />
              </Steps>
            </Card>
          </Col>
          <Col span={12}>
            <Card hoverable title="">
              <Steps direction="vertical" size="small" current={1}>
                <Step title="" description="" />
                <Step title="" description="" />
                <Step title="" description="" />
              </Steps>
            </Card>
          </Col>
        </Row>
        <Row style={{ marginTop: '20px' }}>
          <Col span={24}>
            <Card hoverable title="">
              <Steps current={1} status="error">
                <Step title="" description="" />
                <Step title="" description="" />
                <Step title="" description="" />
              </Steps>
            </Card>
          </Col>
        </Row>
        <Row style={{ marginTop: '20px' }}>
          <Col span={24}>
            <Card hoverable title="">
              <Steps progressDot current={1}>
                <Step title="" description="" />
                <Step title="" description="" />
                <Step title="" description="" />
              </Steps>
            </Card>
          </Col>
        </Row>
      </div >
    )
  }
}


export default StepsDemo;



